body {
  -webkit-font-smoothing: antialiased;
  font-family: 'Overpass Mono', monospace;
  font-size: 1rem;
  line-height: 1.5;
  color: #fff;
}

.container {
  max-width: 1080px;
  margin-left: auto;
  margin-right: auto;
}


.highlight {
  padding-bottom: 0.25em;
  white-space: nowrap;
}

.highlight:before {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  height: 50%;
  width: 100%;
  z-index: 1;
}

.highlight-pink:before {
  background-color: #ff00e6;
}

.highlight-blue:before {
  background-color: #0525ff;
}

.highlight-aqua:before {
  height: 14px;
  background-repeat: repeat-x;
  background-position-y: bottom;
}

.highlight-yellow:before {
  height: 10px;
  background-repeat: repeat-x;
  background-position-y: bottom;
}

@media (min-width: 480px) {
  .m-auto-ns {
    margin: auto;
  }
}

@media (max-width: 480px) {
  .logo {
    max-width: 120px;
  }
}

html {
  scroll-behavior: smooth;
}
a {
  /* text-decoration: none; */
  font-weight: bold;
  margin-right: 20px;
}

.side-image img {
  object-fit: cover;
}
.info-one {
  background: #1b1e6c;
}

.info-two {
  background: #fcff60;
  color: #1b1e6c;
}

.info-three {
  background: #e73030;
  color: #fcff60;
}

.info-four, .info-four a {
  background: #95ef43;
  color:  #e73030;
}

.info-five, .info-five a {
  background: #7137cc;
  color: #95ef43;
}

.info-six {
  background: #fd3dff;
  color: #7137cc;
}

@media (min-width: 480px) {
  .dn-s {
    display: none;
  }
}

.map + div {
  height: 20% !important;
}

